/* We need to add display:inline in order to align the '>>' of the 'read more' link */
.post-excerpt p {
	display:inline;
}

// Import partials from `sass_dir` (defaults to `_sass`)
@import "vendor/fomantic-ui/stylesheets/semantic-ui.scss";
@import "syntax";
@import "starsnonscss";
@import "source-code-block";
@import "premonition";


$themes: (
	dark: (
					primary: white,
					secondary: grey,
					primary-bg-color: black
	),
	light: (
					primary: #4287f5,
					secondary: #666666,
					primary-bg-color: white
	)
);

$sub-themes: (
		java: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(to right, rgba(144, 30, 193, 1) 0%, rgb(107, 73, 238) 51%, rgba(144, 30, 193, 1) 100%)
		),
		git: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(to right, rgba(144, 30, 193, 1) 0%, rgb(107, 73, 238) 51%, rgba(144, 30, 193, 1) 100%)
		),
		concept: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(to right,#051937, #004d7a, #008793, #00bf72, #a8eb12)
		),
		docker: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(to right,#051937, #004d7a, #008793, #00bf72, #a8eb12)
		),
		simple-page: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%)
		),
		oss: (
						primary: rgba(144, 30, 193, 1),
						secondary: rgb(107, 73, 238),
						text-on-gradient-color: white,
						gradient: linear-gradient(90deg, #9ebd13 0%, #008552 100%)
		),
		error: (
			primary: rgba(144, 30, 193, 1),
			secondary: rgb(107, 73, 238),
			text-on-gradient-color: white,
			gradient: linear-gradient(90deg, #9ebd13 0%, #008552 100%)
		)
);

@mixin theme($themes: $themes) {
	@each $theme, $colors in $themes {
		.theme-#{$theme} & {
			@content($colors);
		}
	}
}

@mixin sub-theme($themes: $sub-themes) {
	@each $theme, $colors in $themes {
		.sub-theme-#{$theme} & {
			@content($colors);
		}
	}
}

.gradient-bg {
	@include sub-theme using ($colors) {
		background: map-get($colors, gradient) !important;
		color: map-get($colors, text-on-gradient-color);
	}

	//background: linear-gradient(to right, rgba(144, 30, 193, 1) 0%, rgb(107, 73, 238) 51%, rgba(144, 30, 193, 1) 100%);
	background-size: 200% auto;
	position: relative;
}

.gradient-text {
	@include sub-theme using ($colors) {
		background-image: map-get($colors, gradient);
	}

	//background-image: linear-gradient(to right, rgba(144, 30, 193, 1) 0%, rgb(107, 73, 238) 51%, rgba(144, 30, 193, 1) 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}


body {
	@include theme using ($colors) {
		background: map-get($colors, primary-bg-color);
		color: map-get($colors, primary);
	}
}

.text-center {
	text-align:center;
}

ul.tags-list {
	list-style-type: none;
}

ul.tags-list > li {
	border-radius: 30px;
	padding: 5px 8px;
	display:inline-flex;

	background-image: linear-gradient(to right, #901ec1 0%, #7450fe 51%, #901ec1 100%);
	-webkit-transition: all 500ms;
	transition: all 500ms;
	background-size: 200% auto;
	color: #fff !important;
	border:1px solid white;
}

ul.tags-list > li > a {
	color: white;
}

.btn-gradiant {
		background-image: linear-gradient(to right, #901ec1 0%, #7450fe 51%, #901ec1 100%);
		-webkit-transition: all 500ms;
		transition: all 500ms;
		background-size: 200% auto;
		color: #fff !important;
}

.separator-line {
	text-align: center;
	padding: 15px 0;
	border-top: 1px solid rgba(255, 255, 255, 0.18);
	width:80%;
	margin: 30px auto auto;
}


