/*======================
 Fix footer position
 source: https://github.com/philipwalton/solved-by-flexbox/blob/master/assets/css/components/site.css
=======================*/
body {
	height: 100%;
	display: flex;
	flex-direction: column;
}

footer {
	margin-top:auto;
}

/*======================
   Page template
=======================*/
.article-post {
	@include theme using ($colors) {
		color: map-get($colors, primary);
	}
}

/*======================
    Footer
=======================*/
.footer {
	background-size: 200% auto;
	position: relative;
	width:100%;
	padding-top: 220px;
	bottom: 0;

	@include sub-theme using ($colors) {
		background: map-get($colors, gradient);
		color: map-get($colors, text-on-gradient-color);
	}
}

.footer-bottom > p {
	padding-bottom:20px;
}


.footer-top
{
	@include theme using ($colors) {
		background-color: map-get($colors, primary-bg-color);
	}

	-webkit-mask: url(../theme/footer-top.svg) no-repeat 100% 100%;
	mask: url(../theme/footer-top.svg) no-repeat 100% 100%;
	-webkit-mask-size: cover;
	mask-size: cover;


	//background-image: url(../theme/footer-top.svg);
	background-size: 100% 204px;
	top: 0;
	height: 180px;
	z-index: 1;
	margin-top: -1px;
	position: absolute;
	width: 100%;
	pointer-events: none;
	transform: rotate(180deg);
}

